import React from 'react'
import { Helmet} from 'react-helmet'

const Meta = ({ title, description, keywords }) => {
    return (
        <Helmet>
            <title>{title}</title>
            <meta name = 'description' content={description}/>
            <meta name='keyword' content={keywords}/>
        </Helmet>
    )
}
Meta.defaultProps = {
    title: 'Welcome to SpurSoftwares',
    description: 'software windows microsoft adobe jetbrains webstorm rapidAPI phpmaker intellij clion',
    keywords: 'software windows microsoft adobe jetbrains webstorm rapidAPI phpmaker intellij clion'
}
export default Meta